progress {
  text-align: center;
  margin-top: 10px;
  width: 100%;
  border: 1px solid #AAA;
  position: relative;
  height: 1em;
}

progress::after {
    content: attr(percent)'%';
    position: relative;
    top: -1.1em;
    font-size: 0.8em;
    color: #EEE;
    text-align: center;
}

/* Background Colors  */
progress,                          /* Firefox  */
progress[role][aria-valuenow] {    /* Polyfill */
   background: #222 !important; /* !important is needed by the polyfill */
}

/* Chrome */
progress::-webkit-progress-bar {
    background: #222;
}

/* Foreground Colors   */
/* IE10 */
progress {
    color: #666;
}

/* Firefox */
progress::-moz-progress-bar {
    background: #666;
}

/* Chrome */
progress::-webkit-progress-value {
    background: #666;
}

/* Polyfill */
progress[aria-valuenow]:before  {
    background: #666;
}

.entity {
  border: 2px solid #888;
  background: #333;
  border-radius: 2px;
  margin: 10px;
  padding: 10px;
  position: relative;
}

.entity.active {
  border: 2px solid #eee;
  background: #222;
}
.entity.active strong{
  color: #eee;
}
.position {
  float: right;
  background: #aaa;
  color: black;
  padding: 3px;
}

.portrait {
  float: left;
  text-align: center;
  font-size: 22px;
  line-height: 32px;
  border: 1px solid #666;
  margin: 0;
  margin-right: 10px;

}
